import React from 'react';
import classes from './MainPage.module.scss';

const MainPage = () => {
    return (
        <section className={classes.MainPage}>
            <div className={classes.bigcities}><a href="#">big cities</a></div>
            <div className={classes.smalltowns}><a href="#">small towns</a></div>
            <div className={classes.nature}><a href="#">nature</a></div>
        </section>
    );
};

export default MainPage;