import React from 'react';
import classes from './SubmitForm.module.css';

const SubmitForm = () => {
    return (
        <section className={classes.SubmitForm}>
            <h1>FIND NOTHING?</h1>
            <h3>Let us know what you're looking for. We will send you personalized travel plan.</h3>
            <form className={classes.Form} method="post" action="#">
                    <div>
                        <lable>What's the best trip are you looking for?</lable>
                        <textarea name="message" placeholder="My ideal trip should start with ..."></textarea>                                                                      
                    </div>
                    <div>
                        <label htmlFor="name">Name:</label> 
                        <input type="text" name="name" id="name" placeholder="Your name" required/>                        
                    </div>
                    <div>
                        <label>How do you what us to contct you?</label>
                        <select name="contactMethod" id="contactMethod">
                            <option value="email" selected>email</option>
                            <option value="phone">phone</option>
                            <option value="text">text</option>
                            <option value="mail">mail</option>
                        </select>                         
                    </div>
                    <div>
                        <label htmlFor="email">Email:</label> 
                        <input type="email" name="email" id="email" placeholder="Your email" required/>
                    </div>
                    <div>
                        <label htmlFor="news">Newsletter?</label>
                        <input type="checkbox" name="news" id="news"/>Yes,please
                    </div>
                    <div>
                        <label></label>
                        <input type="submit" value="Send it!"/>
                    </div>
            </form>
            
        </section>
    );
};

export default SubmitForm;