import React from 'react';
import classes from './BigCitiesTextChild.module.scss';

const BigCitiesTextChild = (props) => {
    return (
        <div className={classes.BigCitiesTextChild}>
            <h3>{props.cityname}</h3>
            <p>{props.description}</p>
            <div className={classes.bookInfo1}>
                <img src={props.img} alt={props.author} />
                <div className={classes.bookInfo2}>
                    <h5>{props.author}</h5>
                    <a href="#">
                        <ion-icon name="book-outline"></ion-icon>
                        {props.book}
                    </a>
                </div>  
            </div>
        </div>
    );
};

export default BigCitiesTextChild;